import Button from 'react-bootstrap/esm/Button'

interface ILogoutButton {
	onLogout(): void;
}

export const LogoutButton = (props: ILogoutButton) => {
	return (
		<Button
			size='sm'
			children='Log Out'
			//    ,     
			onClick={() => props.onLogout()}
			variant="outline-danger"
		/>
	)
}